/**
 * @hidden
 */
const DEFAULTS = {
    allowUnsort: true,
    mode: 'single'
};

/**
 * Sets the sort settings of the column.
 */
export type ColumnSortSettings = boolean | {
    /**
     * Enables the removal of the column sorting.
     */
    allowUnsort?: boolean
};

/**
 * Sets the sort settings of the Grid.
 */
export type SortSettings = boolean | ColumnSortSettings & {
    /**
     * The sort mode of the Grid. The available modes are:
     * - `single`
     * - `multiple`
     */
    mode?: 'single' | 'multiple';
}

/**
 * @hidden
 */
export const normalize = (...settings: (SortSettings | ColumnSortSettings)[]): any =>
    Object.assign(DEFAULTS, ...settings);



